@import './newsjournal-styles/values.scss';
$photoWidth: 50px;
$photoWidthMobile: 80px;


.fas.fa-check-circle {
  color: nth($greens, 5);
}

.unopposed {
  font-style: italic;
  color: #666666;

}

.mobile-header {
  display: none;
  @include mobile {
    display: inline-block;
    margin-right: 5px;
  }

  &.mobile-party {
    font-weight: bold;
    padding-bottom: 10px;
    margin-bottom: 10px;
    border-bottom: 1px solid #d7d7d7;
    width: 100%;
    font-size: 20px;
    &.republican {
      color: $republican;
    }

    &.democratic {
      color: $democratic;
    }
  }
}

.App {
  margin: 0 15px;

}

.timestamp {
  margin-bottom: 5px;
  font-size: 14px;
  text-align: right;
}

.race {
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 0;
  }

  h2 {
    margin: 30px auto 0px;
    padding-bottom: 10px;
    font-size: 28px;
    color: #222;
    letter-spacing: 1px;
    text-transform: uppercase;
    font-weight: 500;
    color: #666;
  }


    .candidate,
    .header {
      display: flex;
      align-items: center;
      justify-content: flex-start;
      margin-bottom: 10px;
      padding-bottom: 10px;
      color: #666;

      .g-candidate-photo {
        width: $photoWidth;
        height: $photoWidth;
        border-radius: $photoWidth;
        overflow: hidden;
        margin-right: 10px;
        @include mobile {
          width: $photoWidthMobile;
          height: $photoWidthMobile;
          border-radius: $photoWidthMobile;
        }
        // border: 1px solid #999;
        img {
          width: $photoWidth;
          height: $photoWidth;
          object-fit: cover;
          object-position: top;
          @include mobile {
            width: $photoWidthMobile;
            height: $photoWidthMobile;
          }
        }
      }

      .g-candidate-name {
        width: 350px;
        color: #222;
        font-weight: bold;
        text-transform: capitalize;
        span {
          font-weight: 100;
        }
        @include mobile {
          width: auto;
          font-weight: bold;
        }
      }

      .votes {
        width: calc((100% - 350px) / 4);
      }

      .g-candidate-percent {
        position: relative;

        .votes-bar {
          position: absolute;
          left: 0;
          height: 18px;
          background-color: rgba(#888, 0.25);

          &.Republican {
            background-color: rgba($republican, 0.25);
          }

          &.Democratic {
            background-color: rgba($democratic, 0.25);
          }
          @include mobile {
            display: none;
          }
        }
      }

      .g-candidate-percent,
      .g-candidate-total {
        font-weight: bold !important;
        color: #222 !important;
      }
    }

    .header {
      border-bottom: 1px solid #d7d7d7;

      .g-party {
        font-weight: bold;


        &.Republican {
          color: $republican;
        }

        &.Democratic {
          color: $democratic;
        }
      }

      .votes {
        // text-transform: uppercase;
        font-weight: lighter;
        color: #666;
      }
      @include mobile {
        display: none;
      }
    }

    .candidate {
      border-bottom: 1px solid #f4f4f4;

      &:last-of-type {
        border: none;
      }
      @include mobile {
        flex-wrap: wrap;

        .votes {
          width: 100%;
          padding: 2.5px 0px;

        }
      }
    }

}
